<template>
  <div class="container py-5">
    <NavTabs />
    <form class="w-100" @submit.prevent.stop="registerSubmit">
      <div class="text-center mb-4">
        <h1 class="h3 mb-3 font-weight-normal">
          Sign Up
        </h1>
      </div>

      <div class="form-label-group mb-2">
        <label for="name">Name</label>
        <input
          id="name"
          v-model="name"
          name="name"
          type="text"
          class="form-control"
          placeholder="name"
          autocomplete="username"
          required
          autofocus
        >
      </div>

      <div class="form-label-group mb-2">
        <label for="email">Email</label>
        <input
          id="email"
          v-model="email"
          name="email"
          type="email"
          class="form-control"
          placeholder="email"
          autocomplete="email"
          required
        >
      </div>

      <div class="form-label-group mb-3">
        <label for="password">Password</label>
        <input
          id="password"
          v-model="password"
          name="password"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="new-password"
          required
          pattern="^([a-zA-Z]+\d+|\d+[a-zA-Z]+)[a-zA-Z0-9]*$" 
          minlength="8" 
          maxlength="12" 
          title="請最少輸入8-12位英文與數字"
        >
      </div>

      <div class="form-label-group mb-3">
        <label for="password-check">Password Check</label>
        <input
          id="password-check"
          v-model="passwordCheck"
          name="passwordCheck"
          type="password"
          class="form-control"
          placeholder="Password"
          autocomplete="new-password"
          required
          pattern="^([a-zA-Z]+\d+|\d+[a-zA-Z]+)[a-zA-Z0-9]*$" 
          minlength="8" 
          maxlength="12" 
          title="請最少輸入8-12位英文與數字"
        >
      </div>

      <button
        class="btn btn-lg btn-primary btn-block mb-3"
        type="submit"
        :disabled="isProcessing"
      >
        Submit
      </button>

      <div class="text-center mb-3">
        <p>
          <router-link to="/signin">
            Sign In
          </router-link>
        </p>
      </div>

      <p class="mt-5 mb-3 text-muted text-center">
        &copy; 2017-2018
      </p>
    </form>
  </div>
</template>

<script>
import NavTabs from '../components/NavTabs.vue'
import { Toast } from '../utils/helpers'
import axios from 'axios'

export default {
  components: {
    NavTabs
  },
  data() {
    return {
      name: '',
      email: '',
      password: '',
      passwordCheck: '',
      isProcessing: false
    }
  },
  methods: {
    async registerSubmit() {
      try {
        this.isProcessing = true
        console.log('Register submit button clicked!')
        // 確認四個欄位都有填寫
        console.log('name', this.name)
        console.log('email', this.email)
        console.log('password', this.password)
        console.log('password check', this.passwordCheck)
        if (!this.name.trim() || !this.email || !this.password || !this.passwordCheck) {
          Toast.fire({
            icon: 'warning',
            title: '有欄位未輸入'
          })
          this.isProcessing = false
          return 
        } 

        // email是否重複使用
        axios({
          method: 'post',
          url: 'http://localhost:3000/apis/users/',
          data: {
            email: this.email
          }
        })
          .then(function (response) {
            console.log(response)
            if (response.data.user) {
              Toast.fire({
                icon: 'warning',
                title: '此信箱已註冊!'
              })
              this.isProcessing = false
              return 
            }
          })
        // 密碼和確認密碼一致
        if (this.password !== this.passwordCheck) {
          Toast.fire({
            icon: 'warning',
            title: '密碼與確認密碼不符!'
          })
          this.isProcessing = false
          return
        }

        // 註冊
        axios({
          method: 'post',
          url: 'http://localhost:3000/apis/users/register',
          data: {
            name: this.name,
            email: this.email,
            password: this.password
          }
        })
          .then(response => {
            console.log(response)
            localStorage.setItem('token', response.token)
            localStorage.setItem('username', this.name)
            this.$bus.$emit('logged', 'User logged')
            this.$router.push('/')
            return console.log(response) 
          })

        this.isProcessing = false
      } catch (error) {
          console.log(error)
      }
    }
  }
}
</script>